<template>
  <header>
    <div class="inner">
      <h1 id="logo">
        <a href="#">
          <img :src="require('@/assets/images/logo.png')" alt="대구로" />
          <em>고객지원</em>
        </a>
      </h1>
      <div class="searchWrap">
        <button type="button" class="btnSearch">통합검색 버튼</button>
        <form class="search" @submit.prevent="search">
          <input
            type="text"
            v-model="keyword"
            placeholder="키워드를 입력하여 검색해보세요."
          />
          <button type="submit">검색</button>
        </form>
      </div>
      <button type="button" class="btnMenu">
        <span></span>
        <span></span>
        <span></span>
      </button>
    </div>
  </header>
  <main>
    <div class="inner">
      <ul id="menu">
        <li
          v-for="(row, idx) in serviceUserData"
          :key="idx"
          :class="{ current: selectServiceIdx === idx }"
          @mouseenter="handleMouseEnter(idx)"
          @mouseleave="handleMouseLeave(idx)"
        >
          <a
            href="'#menuCon' + (idx + 1)"
            :class="iconImg[idx]"
            :rel="'menuCon' + (idx + 1)"
            @click.prevent="displayFaqData(idx, row)"
            >{{ row.serviceName }}</a
          >
          <ul
            class="dash"
            v-show-slide:300:ease-in-out="
              active1[idx] || selectServiceIdx === idx
            "
          >
            <li
              v-for="(user, userIndex) in row.users"
              :key="userIndex"
              :class="{ on: this.userCode === user.userCode }"
            >
              <a href="#" @click.prevent="displayFaqData(idx, row, userIndex)">
                {{ user.userName }}
              </a>
            </li>
          </ul>
        </li>
      </ul>
      <div v-show="gbn === 1" id="content">
        <div class="faqWrap" id="menuCon">
          <h2 class="title">
            <span>{{ this.serviceName }}</span>
            <span>{{ this.userName }}</span>
          </h2>
          <ul class="tabBtn">
            <li
              v-for="(row, idx) in type"
              :key="idx"
              :class="{ on: idx === selectTypeIdx }"
            >
              <h3>
                <button type="button" @click="getFaqData(idx, type)">
                  {{ row.typeName }}
                </button>
              </h3>
            </li>
          </ul>
          <div class="faqListWrap">
            <div id="faqList" class="con">
              <dl v-for="(row, idx) in dataList" :key="row.title">
                <transition name="fade" mode="out-in">
                  <dt
                    :ref="`scroll-${idx}`"
                    v-if="active"
                    class="tit"
                    :class="{ on: selectFaqIndex === idx }"
                    @click="getAnswerBody(idx)"
                  >
                    <span class="type">{{ row.questionTitle }}</span>
                  </dt>
                </transition>
                <dd
                  class="txt"
                  v-show-slide:500:ease="selectFaqIndex === idx"
                  v-html="row.answerBody"
                ></dd>
              </dl>
            </div>
          </div>
        </div>
      </div>
      <div v-show="gbn === 2" id="content">
        <div class="faqWrap" id="menuCon">
          <h2 class="title">
            <span>검색결과</span>
            <span>전체&nbsp;{{ this.totalCount }}</span>
          </h2>
          <div class="faqListWrap">
            <div id="faqList" class="con">
              <div v-show="searchDataList.length === 0">
                "{{ this.tempKeyword }}"에 대한 검색 결과가 없습니다.
              </div>
              <div v-for="(group, groupIdx) in searchDataList" :key="groupIdx">
                <dt class="searchTit">
                  <span class="type"
                    >{{ group.serviceName }} > {{ group.userName }}</span
                  >
                </dt>
                <dl v-for="(item, itemIdx) in group.items" :key="item.faqCode">
                  <transition name="fade" mode="out-in">
                    <dt
                      :ref="`scroll-${groupIdx}-${itemIdx}`"
                      v-if="active"
                      class="tit"
                      :class="{
                        on: selectFaqIndex === `${groupIdx}-${itemIdx}`,
                      }"
                      @click="getAnswerBody(`${groupIdx}-${itemIdx}`)"
                    >
                      <span class="type"
                        >[{{ item.typeName }}] {{ item.questionTitle }}</span
                      >
                    </dt>
                  </transition>
                  <dd
                    class="txt"
                    v-show-slide:500:ease="
                      selectFaqIndex === `${groupIdx}-${itemIdx}`
                    "
                    v-html="item.answerBody"
                  ></dd>
                </dl>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </main>

  <div class="mask"></div>
  <button type="button" id="top"><span>top</span></button>
  <div class="mobileMenuWrap">
    <div class="mobileLogo">
      <img :src="require('@/assets/images/logo.png')" alt="대구로" />
      <em>고객지원</em>
      <button type="button" class="close">모바일 메뉴 닫기</button>
    </div>
    <ul class="mobileMenu">
      <li v-for="(row, idx) in serviceUserData" :key="idx">
        <em :class="'tit ' + iconImg[idx]">{{ row.serviceName }}</em>
        <ul class="dash dot">
          <li
            v-for="(user, userIndex) in row.users"
            :key="userIndex"
            :class="{ on: this.userCode === user.userCode }"
          >
            <a
              href="#"
              @click.prevent="
                displayFaqData(idx, row, userIndex), removeMobileMenu()
              "
            >
              {{ user.userName }}
            </a>
          </li>
        </ul>
      </li>
    </ul>
  </div>
</template>

<script>
import $ from "jquery";

export default {
  name: "HelloWorld",

  data: () => ({
    iconImg: [
      "menuIco delivery",
      "menuIco taxi",
      "menuIco reservation",
      "menuIco flower",
      "menuIco bus",
    ],
    keyword: "",
    tempKeyword: "",
    serviceCode: "",
    serviceName: "",
    userCode: "",
    userName: "",
    type: [],
    typeCode: "",
    serviceUserData: [],
    dataList: [],
    searchDataList: [],
    selectServiceIdx: 0,
    selectTypeIdx: 0,
    totalCount: 0,
    gbn: 1,
    selectFaqIndex: null,
    active: true,
    active1: {},
  }),
  mounted() {
    this.getFaqServiceUserList();
    function mobileRemove() {
      $(".mobileMenuWrap").removeClass("on");
      $(".mask").removeClass("on");
    }
    $(function () {
      $(".btnSearch").click(function () {
        $(this).toggleClass("on");
        $(".search").slideToggle();
      });

      $(".btnMenu").click(function () {
        $(this).toggleClass("on");
        $(".mobileMenuWrap").addClass("on");
        $(".mask").toggleClass("on");
      });

      $(".mobileMenuWrap .close").click(function () {
        mobileRemove();
      });

      $(window).on("load resize", function () {
        var w = $(window).width();
        var x = 1024;
        if (w > x) {
          mobileRemove();
        }
      });

      // top
      $("#top").click(function () {
        $("html, body").stop().animate(
          {
            scrollTop: 0,
          },
          500
        );
      });

      $(window).scroll(function () {
        if ($(this).scrollTop() > 100) {
          $("#top").addClass("on");
        } else {
          $("#top").removeClass("on");
        }
      });
    });
  },
  methods: {
    async getFaqServiceUserList() {
      await this.$axios
        .get("/SupportFAQ/GetFAQService")
        .then(async (response) => {
          for (const item of response.data.data) {
            await this.$axios
              .get(`/SupportFAQ/GetFAQUser?serviceCode=${item.serviceCode}`)
              .then(async (response) => {
                const users = response.data.data.map((user) => {
                  return {
                    userCode: user.userCode,
                    userName: user.userName,
                    serviceCode: user.serviceCode,
                    memo: user.memo,
                    dispSeq: user.dispSeq,
                    categoryCode: user.categoryCode,
                    useGbn: user.useGbn,
                  };
                });
                this.serviceUserData.push({
                  serviceName: item.serviceName,
                  users: users,
                });
              });
          }
        });
      this.serviceCode = this.serviceUserData[0].users[0].serviceCode;
      this.serviceName = this.serviceUserData[0].serviceName;
      this.userCode = this.serviceUserData[0].users[0].userCode;
      this.userName = this.serviceUserData[0].users[0].userName;
      await this.getFaqTypeList(this.serviceCode, this.userCode);
    },

    displayFaqData(selectedIdx, serviceUserData, selectedUserIdx = 0) {
      this.gbn = 1;
      this.keyword = "";
      this.selectServiceIdx = selectedIdx;
      this.selectTypeIdx = 0;
      const selectedUser = serviceUserData.users[selectedUserIdx];
      this.userCode = selectedUser.userCode;
      this.serviceName = serviceUserData.serviceName;
      this.userName = selectedUser.userName;

      this.getFaqTypeList(selectedUser.serviceCode, selectedUser.userCode);
    },

    async getFaqTypeList(serviceCode, userCode) {
      await this.$axios
        .get(
          `/SupportFAQ/GetFAQType?serviceCode=${serviceCode}&userCode=${userCode}`
        )
        .then(async (response) => {
          this.type = response.data.data.map((type) => {
            return {
              typeCode: type.typeCode,
              typeName: type.typeName,
              userCode: type.userCode,
              serviceCode: type.serviceCode,
              categoryCode: type.categoryCode,
              memo: type.memo,
              dispSeq: type.dispSeq,
              useGbn: type.useGbn,
            };
          });
        });
      this.typeCode = this.type[0] ? this.type[0].typeCode : 0;
      await this.getFaqList();
    },

    async getFaqList() {
      this.dataList = null;
      await this.$axios
        .get(
          `/SupportFAQ/GetFAQ?useGbn=Y&serviceCode=${this.serviceCode}&userCode=${this.userCode}&typeCode=${this.typeCode}`
        )
        .then(async (response) => {
          this.dataList = response.data.data.map((data) => {
            return {
              faqCode: data.faqCode,
              serviceCode: data.serviceCode,
              serviceName: data.serviceName,
              userCode: data.userCode,
              userName: data.userName,
              typeCode: data.typeCode,
              typeName: data.typeName,
              useGbn: data.useGbn,
              questionTitle: data.questionTitle,
              answerBody: data.answerBody,
              insDate: data.insDate,
              insUcode: data.insUcode,
              insUname: data.insUname,
              modDate: data.modDate,
              modUcode: data.modUcode,
              modUname: data.modUname,
            };
          });
        });
    },

    getFaqData(idx, type) {
      if (this.selectTypeIdx === idx) {
        return;
      }
      this.selectFaqIndex = null;
      this.serviceCode = type[idx].serviceCode;
      this.userCode = type[idx].userCode;
      this.typeCode = type[idx].typeCode;
      this.getFaqList();
      this.active = false;

      setTimeout(() => {
        this.selectTypeIdx = idx;
        this.$nextTick(() => {
          this.active = true;
        });
      }, 100);
    },

    getAnswerBody(idx) {
      this.selectFaqIndex = this.selectFaqIndex === idx ? null : idx;

      // if (this.selectFaqIndex !== null) {
      //   const section = this.$refs[`scroll-${idx}`][0];

      //   if (section) {
      //     setTimeout(() => {
      //       section.scrollIntoView({ behavior: "smooth", block: "center" });
      //     }, 400); //
      //   }
      // }
    },

    async search() {
      this.gbn = 2;
      this.tempKeyword = this.keyword;
      this.totalCount = 0;
      await this.$axios
        .get(`/SupportFAQ/GetFAQ?search=${this.keyword}`)
        .then((response) => {
          const groupedData = response.data.data.reduce((acc, cur) => {
            const key = `${cur.serviceName}_${cur.userName}`;

            if (!acc[key]) {
              acc[key] = {
                serviceName: cur.serviceName,
                userName: cur.userName,
                items: [],
              };
            }

            acc[key].items.push({
              faqCode: cur.faqCode,
              serviceCode: cur.serviceCode,
              userCode: cur.userCode,
              typeCode: cur.typeCode,
              typeName: cur.typeName,
              useGbn: cur.useGbn,
              questionTitle: cur.questionTitle,
              answerBody: cur.answerBody,
              insDate: cur.insDate,
              insUcode: cur.insUcode,
              insUname: cur.insUname,
              modDate: cur.modDate,
              modUcode: cur.modUcode,
              modUname: cur.modUname,
            });
            this.totalCount += 1;
            return acc;
          }, {});

          this.searchDataList = Object.values(groupedData);
        });
    },
    handleMouseEnter(idx) {
      this.active1[idx] = true;
    },
    handleMouseLeave(idx) {
      this.active1[idx] = false;
    },

    removeMobileMenu() {
      $(".mobileMenuWrap").removeClass("on");
      $(".mask").removeClass("on");
    },
  },
};
</script>
<style>
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.3s;
}

.fade-enter-from,
.fade-leave-to {
  opacity: 0;
}
</style>
